@import '/app/global.scss';

/** List component css start here **/
.list_component {
  width: 100%;
  border-radius: 3px;
}
.list_component .list_component_outer {
  width: 100%;
  height: 100%;
  overflow-y: overlay;
  outline: none;
}
.list_component .list_component_outer::-webkit-scrollbar-track {
  border-radius: 3px;
  background-color: transparent;
}
.list_component .list_component_outer::-webkit-scrollbar {
  width: 5px;
  background-color: transparent;
}
.list_component .list_component_outer::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background-color: getcolor(scrollbar_color);
}
.list_component .list_component_outer ul {
  width: 100%;
  list-style-type: none;
  padding: 0;
  margin: 0;
  border: 3px;
}
.list_component .list_component_outer li.selected {
  color: red;
  background-color: getcolor(select_hover);
}
.list_component .list_component_outer .selected span {
  color: getcolor(heading_color);
}
.list_component .list_component_outer .selected div {
  color: getcolor(heading_color);
}

/** List component css end here **/
