@import '/app/global.scss';

/** Toast Notification component bar css start here **/

.toast_notification_component {
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  background-color: #018351;
  box-shadow: 0px 5px 10px 0px rgba(1, 152, 95, 0.2); ;
  z-index: 999;
  opacity: 1;
  width: auto;
  border-radius: 4px;
  padding: 10px 20px;
}
.toast_notification_component.floating {
  animation-delay: 0.2s;
  animation-duration: 0.6s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-out;
  animation-name: slideInDown;
  top: -100%;
}
@keyframes slideInDown {
  0% {
    top: -100%;
    opacity: 0;
    z-index: -1;
    visibility: hidden;
  }
  100% {
    top: 70px;
    opacity: 1;
    z-index: 999;
    visibility: visible;
  }
}
.toast_notification_component.autoremove {
  animation-delay: 0.2s;
  animation-duration: 0.6s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-out;
  animation-name: autoRemove;
  top: 70px;
}
.toast_notification_component.floating.bottom {
  bottom: -100%;
  animation-name: slideInUp;
  top:unset;
}
.toast_notification_component.autoremove.bottom {
  bottom: 30px;
  top: unset;
  animation-name: autoRemoveBottom;
}
@keyframes slideInUp {
  0% {
    top: unset;
    bottom: -100%;
    opacity: 0;
    z-index: -1;
    visibility: hidden;
  }
  100% {
    top: unset;
    bottom: 30px;
    opacity: 1;
    z-index: 999;
    visibility: visible;
  }
}

@keyframes autoRemove {
  0% {
    top: 70px;
    opacity: 1;
    z-index: 999;
    visibility: visible;
  }
  100% {
    top: -100%;
    opacity: 0;
    z-index: -1;
    visibility: hidden;
  }
}
@keyframes autoRemoveBottom {
  0% {
    bottom: 30px;
    top: unset;
    opacity: 1;
    z-index: 999;
    visibility: visible;
  }
  100% {
    bottom: -100%;
    top: unset;
    opacity: 0;
    z-index: -1;
    visibility: hidden;
  }
}
.toast_notification_component .toast_notification_title {
  color: #fff;
  font-size: 10px;
  line-height: 10px;
  font-family: 'Gilroy-Regular';
}
.toast_notification_component.pos_absolute {
  position: absolute;
}
.toast_notification_component.left {
  left: 20px;
  right: initial;
  transform: none;
}
.toast_notification_component.right {
  left: initial;
  right: 20px;
  transform: none;
}
.toast_notification_component .toast_notification_component_container {
  display: flex;
  align-items: center;
  justify-content: center;
}
.toast_notification_component  .toast_notification_component_container.left {
  justify-content: flex-start;
}
.toast_notification_component .toast_notification_component_container.right {
  justify-content: flex-end;
}
.toast_notification_component .toast_notification_component_container .toast_notification_icon {
  width: 12px;
  display: block;
  margin-right: 5px;
}

@media only screen and (max-width: 1399px){
  .toast_notification_component .toast_notification_title {
    font-size: 10px;
    line-height: 10px;
  }
  .toast_notification_component .toast_notification_component_container .toast_notification_icon {
    width: 10px;
  }
  .toast_notification_component.autoremove {
     top:50px;
  }
  @keyframes slideInDown {
    0% {
      top: -100%;
    }
    100% {
      top: 50px;
    }
  }
  @keyframes autoRemove {
    0% {
      top: 50px;
    }
    100% {
      top: -100%;
    }
  }
}

/** Toast Notification component bar css end here **/
