@import '/app/global.scss';

/** Tooltip component css start here **/
.tooltip_component {
  position: relative;
  cursor: pointer;
}
.tooltip_component.inline {
  display: inline-block;
}
.tooltip_component.disable {
  cursor: not-allowed;
}
.tooltip_component .tooltip_position {
  visibility: hidden;
  position: absolute;
  left: -1000000%;
  top: 0;
}
.tooltip_component.disable .tooltip_position {
  display: none;
}
.tooltip_component.show .tooltip_position {
  display: none;
}
.tooltip_component:hover .tooltip_position {
  z-index: 9999;
  width: auto;
  font-size: 10px;
  line-height: 12px;
  font-family: 'Gilroy-Medium';
  color: #fff;
  padding: 4px 6px;
  border-radius: 3px;
  background: getcolor(theme_color);
  box-shadow: 0 0 7px 0 rgba(0, 0, 0, 0.05);
}
.tooltip_component .tooltip_position.colored {
  background: getcolor(theme_color);
  color: #fff;
}

/** Tooltip component default css start here **/
.tooltip_component .tooltip_position.default {
  background: getcolor(tooltip_default_background);
  color: getcolor(tooltip_title_color);
  box-shadow: 0 0 7px 0 rgba(0, 0, 0, 0.05);
}
.tooltip_component .tooltip_position.default.left::after {
  border-left-color: getcolor(tooltip_default_background);
}
.tooltip_component .tooltip_position.default.right::after {
  border-right-color: getcolor(tooltip_default_background);
}
.tooltip_component .tooltip_position.default.top::after {
  border-top-color: getcolor(tooltip_default_background);
}
.tooltip_component .tooltip_position.default.bottom::after {
  border-bottom-color: getcolor(tooltip_default_background);
}

/** Tooltip Component default css end here **/

/** Tooltip Component Warpping css start here **/
.tooltip_component .tooltip_position.wrap {
  min-width: 60px;
}
.tooltip_component .tooltip_position.nowrap {
  white-space: nowrap;
}

/** Tooltip Component Warpping css end here **/

/** Tooltip Component text alignment css start here **/
.tooltip_component .tooltip_position.text_center {
  text-align: center;
}
.tooltip_component .tooltip_position.text_left {
  text-align: left;
}
.tooltip_component .tooltip_position.text_right {
  text-align: right;
}

/** Tooltip Component text alignment css end here **/

/** Tooltip Component placement css end here **/

.tooltip_component .tooltip_position::after {
  content: '';
  position: absolute;
}
.tooltip_component .tooltip_position::before {
  content: '';
  position: absolute;
}
.tooltip_component .tooltip_position.arrow_disable::after,
.tooltip_component .tooltip_position.arrow_disable::before {
  display: none;
}
.tooltip_component .left::after {
  top: 50%;
  left: 100%;
  transform: translateY(-50%);
  width: 0;
  height: 0;
  border-left: 6px solid getcolor(theme_color);
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
}
.tooltip_component:hover .left {
  right: calc(100% + 6px);
  top: 50%;
  animation: tooltips-vert 200ms ease forwards;
  animation-delay: 300ms;
  left: initial;
}
.tooltip_component .bottom::after {
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-bottom: 6px solid getcolor(theme_color);
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
}
.tooltip_component:hover .bottom {
  left: 50%;
  transform: translateX(-50%);
  top: calc(100% + 6px);
  animation: tooltips-hori 200ms ease forwards;
  animation-delay: 300ms;
  bottom: unset !important;
}
.tooltip_component .top::after {
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-top: 6px solid getcolor(theme_color);
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
}
.tooltip_component:hover .top {
  left: 50%;
  transform: translateX(-50%);
  bottom: calc(100% + 6px);
  animation: tooltips-hori 200ms ease forwards;
  animation-delay: 300ms;
  top: unset;
}
.tooltip_component .right::after {
  top: 50%;
  right: 100%;
  transform: translateY(-50%);
  width: 0;
  height: 0;
  border-right: 6px solid getcolor(theme_color);
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
}
.tooltip_component:hover .right {
  left: calc(100% + 6px);
  top: 50%;
  animation: tooltips-vert 200ms ease forwards;
  animation-delay: 300ms;
}

@keyframes tooltips-vert {
  from {
    visibility: hidden;
    opacity: 0;
    transform: translate(10%, -50%);
  }
  to {
    visibility: visible;
    opacity: 1;
    transform: translate(0, -50%);
  }
}
@keyframes tooltips-hori {
  from {
    visibility: hidden;
    opacity: 0;
    transform: translate(-50%, -10%);
  }
  to {
    visibility: visible;
    opacity: 1;
    transform: translate(-50%, 0);
  }
}

/** Tooltip Component placement end end here **/

/** Tooltip component css end here **/
