@import '/app/global.scss';

/** Input Component css start here **/
.input_component {
  position: relative;
}
.input_component .input_component_container {
  position: relative;
}
.input_component .input_component_container form {
  position: relative;
}
.input_component .input_component_container form.input_form_gap input {
  padding-right: 65px;
}
.input_component .input_component_container form .input_component_loader {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: getcolor(input_loader_background);
  opacity: 0.9;
  border-radius: 5px;
}
.input_component
  .input_component_container
  form
  .input_component_loader
  .input_loader_icon {
  position: relative;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.input_component .input_component_container .input_component_icon {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  padding-left: 8px;
  border-left: 1px solid getcolor(input_color_border);
}
.input_component .input_component_container .input_component_icon img {
  width: 12px;
}
.input_component .clear_icon {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
}
.input_component .input_form_gap .clear_icon {
  right: 45px;
}
.input_component label img {
  width: 14px;
  margin-left: 5px;
  display: inline-block;
}
.input_component .clear_icon img {
  width: 9px;
  display: block;
  cursor: pointer;
}
.input_component .input_icon {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
}
.input_component .input_icon img {
  width: 12px;
  display: block;
  cursor: pointer;
}
.input_component input {
  font-size: 16px;
  padding: 19.5px 16px;
  font-family: 'Gilroy-Regular';
  position: relative;
  color: getcolor(input_color_text);
  border: 1px solid getcolor(input_color_border);
  border-radius: 5px;
  background: none;
  width: 100%;
  padding-right: 30px;
  outline: none;
}
.input_component input:disabled {
  opacity: 0.6;
}
.input_component input:focus {
  border-color: #909090;
}
.input_component .input_space input {
  padding-right: 50px;
}
.input_component .input_space .clear_icon {
  border-right: 1px solid getcolor(input_divider_color);
  right: 30px;
  margin-right: 2px;
  padding-right: 5px;
}
.input_component.disable {
  opacity: 0.4;
  pointer-events: none;
}
.input_component label.animate_label {
  position: absolute;
  top: 50%;
  left: 0;
  font-size: 16px;
  line-height: 16px;
  font-family: 'Gilroy-Regular';
  color: getcolor(text_color);
  transition: font-size 0.2s ease, transform 0.2s ease, transform-origin 0.2s,
    top 0.2s;
  transform: translate(0, -50%) scale(1);
  transform-origin: left top;
  background-color: getcolor(dashboard_background);
  margin: 0 8px;
  padding: 0 8px;
  pointer-events: none;
}
.input_component label.animate_label::before {
  content: '';
  position: absolute;
}
.input_component label.animate_label::after {
  content: '';
  position: absolute;
}

.input_component .input_label_wrapper label.animate_label {
  color: getcolor(text_color);
  font-size: 75%;
  line-height: 14px;
  top: 0;
  transform: translate(0, -7px) scale(0.9);
}
.input_component .label_disable {
  opacity: 0.5 !important;
}
.input_component .input_label_wrapper label.animate_label img {
  transform: translate(0, 0) scale(0.9);
  vertical-align: top;
}
.input_component.input_error .input_label_wrapper label {
  color: #d7322d;
}
.input_component.input_error input {
  border-color: #d7322d;
}
.input_component.inpur_error_disable input {
  border-color: getcolor(input_border_color);
}
.input_component.input_error .input_error_text {
  border: 1px solid rgba(215, 50, 45, 0.25);
  background-color: rgba(215, 50, 45, 0.07);
  color: #d7322d;
  padding: 12px 15px;
  font-size: 14px;
  line-height: 14px;
  margin-top: 5px;
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  padding-left: 35px;
  top: 100%;
  font-family: 'Gilroy-Regular';
  border-radius: 5px;
  word-break: break-word;
  text-align: left;
}
.input_component.input_error .input_error_text.input_error_text_default {
  background-color: transparent;
  padding: 0;
  border: none;
  padding-left: 20px;
  margin-top: 8px;
  position: relative;
  transition: all 0.2s linear;
}
.input_component.input_error
  .input_error_text.input_error_text_default::before {
  left: 0;
}
.input_component.input_error .input_error_text.top {
  top: calc(-100%);
  margin-top: 0;
}
.input_component.input_error .input_error_text.bottom {
  top: 100%;
  margin-top: 20px;
}
.input_component.input_error .input_error_text::before {
  content: '';
  position: absolute;
  background-image: url('../../icons/warning.svg');
  background-repeat: no-repeat;
  background-position: left center;
  width: 14px;
  height: 14px;
  line-height: 14px;
  left: 10px;
}
@media only screen and (max-width: 1600px) {
  .input_component input {
    font-size: 14px;
    padding: 16px 16px;
    padding-right: 25px;
  }
  .input_component .input_component_container .input_component_icon img {
    width: 11px;
  }

  .input_component .clear_icon img {
    width: 7px;
  }
  .input_component .input_icon img {
    width: 10px;
  }

  .input_component label.animate_label {
    font-size: 14px;
    line-height: 14px;
  }
  .input_component .input_label_wrapper label.animate_label {
    transform: translate(0, -7px) scale(0.9);
    line-height: 14px;
  }
  .input_component.input_error .input_error_text {
    font-size: 11px;
    line-height: 11px;
    padding: 10px 12px;
    padding-left: 30px;
  }
  .input_component.input_error .input_error_text::before {
    width: 11px;
    height: 11px;
    line-height: 11px;
  }
}
@media only screen and (max-width: 1399px) {
  .input_component input {
    font-size: 12px;
    padding: 12px 12px;
    padding-right: 25px;
  }
  .input_component .input_component_container form.input_form_gap input {
    padding-right: 55px;
  }
  .input_component .input_form_gap .clear_icon {
    right: 40px;
  }
  .input_component .input_component_container .input_component_icon {
    right: 10px;
  }
  .input_component label.animate_label {
    font-size: 12px;
    margin: 0 6px;
    padding: 0 6px;
  }
  .input_component .input_label_wrapper label.animate_label {
    transform: translate(0, -6px) scale(0.9);
    line-height: 12px;
  }
  .input_component .clear_icon {
    position: absolute;
    right: 12px;
  }
  .input_component.input_error .input_error_text {
    font-size: 10px;
    line-height: 10px;
    padding: 9px 10px;
    padding-left: 30px;
  }
  .input_component.input_error .input_error_text::before {
    width: 10px;
    height: 10px;
    line-height: 10px;
  }
  .input_component.input_error .input_error_text.input_error_text_default {
    padding-left: 15px;
  }
}

@media only screen and (max-width: 1199px) {
  .input_component label.animate_label {
    transform: translate(0, -54%) scale(1);
  }
}
