@import '/app/global.scss';

/** iconBox component css start here **/

.disabled {
  cursor: not-allowed;
}
.badge_component {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color:getcolor(theme_color);
  font-size: 12px;
  line-height: 12px;
  font-family: 'Gilroy-Regular';
  text-align: center;
  padding: 2px;
  color: getcolor(badge_text_color);
  vertical-align: baseline;
  min-width: 18px;
}
.disabled .badge_component {
  opacity: 0.6;
}

/** iconBox component css for space start here **/
.badge_component.sm {
  width: 25px;
  height: 25px;
}
.badge_component.md {
  width: 40px;
  height: 40px;
}
.badge_component.lg {
  width: 45px;
  height: 45px;
}

/** iconBox component css for space end here **/
.badge_component .icon {
  width: 14px;
  display: block;
}

/** iconBox component css for color default, active and fill start here **/
.badge_component.outlined {
  border: 1px solid getcolor(badge_border_color);
  background: getcolor(badge_outline_background_color);
}
.badge_component.filled {
  border: 1px solid getcolor(theme_color);
  background: getcolor(theme_color);
}
.badge_component.filled:hover {
  background: getcolor(theme_color_onhover);
  border-color: getcolor(theme_color_onhover);
}
.badge_component.filled.active {
  border-color: getcolor(theme_color_onhover);
  background: getcolor(theme_color_onhover);
}
.badge_component.outlined.active {
  border: 1px solid getcolor(theme_color);
  background: getcolor(theme_color);
}
.badge_component.default.active {
  background: getcolor(badge_outline_background_color);
}
.badge_component.default:hover {
  background: getcolor(badge_outline_background_color);
}

/** iconBox component css for color default, active and fill end here **/

@media only screen and (max-width: 1399px) {
  .badge_component.md {
    width: 30px;
    height: 30px;
  }
  .badge_component.lg {
    width: 40px;
    height: 40px;
  }
  .badge_component {
    font-size: 10px;
    line-height: 11px;

  }
}

