@import '/app/global.scss';

/** tabs Component css start here **/
.tabs_component {
  display: block;
}

.tabs_component .tab_pills ul {
  list-style: none;
  margin: 0;
  border-bottom: 1px solid getcolor(tabs_border_color);
  padding: 0 40px;
}

.tabs_component .tab_pills ul li {
  display: inline-block;
  padding: 10px 15px;
  font-size: 16px;
  line-height: 16px;
  font-family: 'Gilroy-Medium';
  border-width: 1px 0 0 1px;
  border-style: solid;
  border-color: getcolor(tabs_border_color);
  cursor: pointer;
  position: relative;
  background-color: getcolor(tabs_background_color);
  color: getcolor(tabs_text_color);
}
.tabs_component .tab_pills ul li:hover {
  background-color: getcolor(tabs_active_background_color);
}
.tabs_component .tab_pills ul li:last-child {
  border-right: 1px solid getcolor(tabs_border_color);
}
.tabs_component .tab_pills ul .tab_link {
  color: getcolor(tabs_text_color);
}
.tabs_component .tab_pills ul li.active {
  color: getcolor(tabs_active_text_color);
  background-color: getcolor(tabs_active_background_color);
  border-color: getcolor(tabs_active_border_color);
}
.tabs_component .tab_pills ul li.active::after {
  content: '';
  position: absolute;
  width: 100%;
  left: 0;
  right: 0;
  height: 2px;
  background: getcolor(theme_color);
  bottom: 0;
}
.tabs_component .tab_pills ul li.disable {
  cursor: default;
  opacity: 0.5;
  background-color: transparent;
}
.tabs_component .tab_pills ul li.disable:hover {
  background-color: transparent;
}
.tabs_component .tab_content {
  padding: 30px;
}

.tabs_component .tab_content_tag {
  font-size: 10px;
  line-height: 10px;
  color: getcolor(heading_color);
  padding: 3px 6px;
  border-radius: 4px;
  background-color: getcolor(tab_list_beta_bg_color);
  margin-left: 10px;
  text-transform: uppercase;
  font-family: 'Gilroy-Bold';
  min-width: 30px;
}

.tabs_component .tab_vertical .tab_pills ul {
  list-style: none;
  margin: 0;
  padding: 0;
  border-bottom: 0;
  border-right: 0;
}

.tabs_component .tab_vertical .tab_pills ul li {
  width: 100%;
  border-width: 0 0 1px 0;
  padding: 12px 15px;
  display: flex;
  align-items: center;
  margin: 3px 0;
}

.tabs_component .tab_vertical .tab_pills ul li:hover {
  background-color: getcolor(vertical_tab_hover_color);
}

.tabs_component .tab_vertical .tab_pills ul li:last-child {
  border-bottom: 0;
  border-right: 0;
}
.tabs_component .tab_vertical .tab_pills ul li.active {
  background-color: getcolor(vertical_tab_hover_color);
}
.tabs_component .tab_vertical .tab_pills ul li span {
  margin-left: 10px;
  color: getcolor(vertical_tab_li_color);
}
.tabs_component .tab_vertical .tab_pills ul li.active span {
  color: getcolor(vertical_tab_li_hover_color);
}

.tabs_component .tab_vertical .tab_pills ul li.active::after {
  content: none;
}

.tabs_component .tab_vertical .tab_pills ul li:hover span {
  color: getcolor(vertical_tab_li_hover_color);
}

.tabs_component .tab_vertical .tab_content {
  padding: 0;
}

@media only screen and (max-width: 1399px) {
  .tabs_component .tab_pills ul {
    padding: 0 30px;
  }
  .tabs_component .tab_content_tag {
    font-size: 8px;
    line-height: 8px;
  }
}

/** tabPanel Component css end here **/
