@import '/app/global.scss';

/** Text Area Component css start here **/
.text_area_component {
  position: relative;
}
.text_area_component .text_area_component_container {
  position: relative;
}
.text_area_component .text_area_component_container .textarea_limit{
  font-size: 12px;
  line-height: 12px;
  color: getcolor(sub_heading_color);
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
}

.text_area_component textarea {
  font-size: 16px;
  line-height: 16px;
  padding: 20px 16px;
  font-family: 'Gilroy-Regular';
  position: relative;
  color: getcolor(input_color_text);
  border: 1px solid getcolor(input_color_border);
  border-radius: 5px;
  background: none;
  width: 100%;
  resize: none;
  outline: none;
}
.text_area_component textarea:focus {
  border-color: #909090;
}
.text_area_component textarea::placeholder {
  color: getcolor(text_color);
}
.text_area_component.disable{
  opacity: 0.4;
  pointer-events: none;
}
.text_area_component label.animate_label {
  position: absolute;
  top: 14%;
  left: 0;
  font-size: 16px;
  line-height: 16px;
  font-family: 'Gilroy-Regular';
  color: getcolor(text_color);
  transition: font-size 0.2s ease, transform 0.2s ease, transform-origin 0.2s , top 0.2s;
  transform: translate(0, -8%) scale(1);
  transform-origin: left top;
  background-color: getcolor(dashboard_background);
  margin: 0 8px;
  padding: 0 8px;
  pointer-events: none;
}
.text_area_component .floating_label label.animate_label {
  color: getcolor(text_color);
  font-size: 75%;
  line-height: 16px;
  top: 0;
  transform: translate(0, -8px) scale(0.9);
}

.text_area_component.error .floating_label label  {
  color: #d7322d;
}
.text_area_component.error textarea {
  border-color: #d7322d;
}
.text_area_component span {
  display: none;
}
.text_area_component.error span {
  border: 1px solid rgba(215, 50, 45, 0.25);
  background-color: rgba(215, 50, 45, 0.07);
  color: #d7322d;
  padding:10px 15px;
  font-size: 10px;
  line-height: 10px;
  margin-top: 5px;
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  padding-left: 20px;
  top: 100%;
  font-family: 'Gilroy-Regular';
  border-radius: 5px;
}
.text_area_component.error span::before {
  content: '';
  position: absolute;
  background-image: url('../../icons/warning.svg');
  background-repeat: no-repeat;
  background-position: left center;
  width: 12px;
  height: 12px;
  line-height: 12px;
  left: 5px;
}
@media only screen and (max-width: 1600px){
  .text_area_component textarea {
    font-size: 14px;
    line-height: 14px;
    padding: 16px 16px;
  }
  .text_area_component label.animate_label {
    font-size: 14px;
    line-height: 14px;
    top: 13%;
  }
  .text_area_component .floating_label label.animate_label {
    transform: translate(0, -7px) scale(0.9);
    line-height: 14px;
  }
}

@media only screen and (max-width: 1399px){
  .text_area_component textarea {
    font-size: 12px;
    line-height: 12px;
    padding: 12px 12px;
  }
  .text_area_component label.animate_label {
    font-size: 12px;
    line-height: 12px;
    margin: 0 6px;
    padding: 0 6px;
    top: 12%;
  }
  .text_area_component .floating_label label.animate_label {
    transform: translate(0, -6px) scale(0.9);
    line-height: 12px;
  }
  .text_area_component .text_area_component_container .textarea_limit{
    font-size: 10px;
    line-height: 10px;
  }
}

/** Text Area component css end here **/
