@import '/app/global.scss';

/** Tag component css start here **/
.tag_component {
  padding: 8px 10px;
  background-color: getcolor(hoc_border_color);
  color: getcolor(tag_text_color);
  font-family: 'Gilroy-Medium';
  font-size: 12px;
  line-height: 12px;
  text-align: center;
  border-radius: 5px;
  margin: var(--gap) 0 0 var(--gap);
  display: grid;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: relative;
  border: 1px solid getcolor(hoc_border_color);
}
.tag_component:hover {
  color: getcolor(theme_color);;
}

/** Tag component sizes css start here **/
.tag_component.md {
  font-size: 14px;
  line-height: 14px;
  padding: 10px 14px;
}
.tag_component.sm{
  padding: 8px 10px;
  font-size: 12px;
  line-height: 12px;
}
.tag_component.lg {
  font-size: 16px;
  line-height: 16px;
  padding: 12px 16px;
}

/** Tag component sizes css start here **/

.tag_component.default {
  border-radius: 5px;
}
.tag_component.rounded {
  border-radius: 10em;
}
.tag_component.square {
  border-radius: 0;
}

/** Tag Component Tooltip start here **/
.tag_component.tooltip_show{
  position: relative;
}
.tag_component.tooltip_show::after{
    visibility: hidden;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0;
    content: attr(tool-tips);
    padding: 5px;
    z-index: 999;
    background: getcolor(tooltip_tag_background_color);
    border: 1px solid getcolor(tooltip_tag_background_color);
    color: getcolor(tooltip_text_collapsed_color);
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    width: auto;
    box-shadow: 0 0 7px 0 rgba(0, 0, 0, 0.05);
    transition-delay: .5s;
    font-size: 12px;
    line-height: 16px;
}
.tag_component.tooltip_show::before{
  position: absolute;
    visibility: hidden;
    width: 0;
    height: 0;
    left: 50%;
    bottom: 0;
    opacity: 0;
    content: "";
    border-style: solid;
    border-width: 8px 8px 0 8px;
    border-color: getcolor(tooltip_tag_background_color) transparent transparent transparent;
    transition-delay: .5s;
}
.tag_component.tooltip_show:hover::after{
  visibility: visible;
  opacity: 1;
  z-index: 1;
  bottom: 135%;
}
.tag_component.tooltip_show:hover::before{
  visibility: visible;
  opacity: 1;
  z-index: 1;
  bottom: 110%;
}
.tag_component.tooltip_show.tag_disabled{
  cursor: default;
}

/** Tag Component Tooltip css end here **/

/** Tag component variants css start here **/
.tag_component.contained {
  background-color: getcolor(hoc_border_color);
  border: 1px solid getcolor(hoc_border_color);
  color: getcolor(subtitle_text_color);
}
.tag_component.contained:hover {
  background-color: getcolor(hoc_border_color);
  color: getcolor(theme_color);
  border: 1px solid getcolor(hoc_border_color);
  box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.2);
}
.tag_component.outline {
  background-color: getcolor(tag_outline_background_color);
  border: 1px solid getcolor(tag_outline_border_color);
  color: getcolor(heading_color);
}
.tag_component.outline:hover {
  background-color: getcolor(tag_outline_background_color);
  color: getcolor(theme_color);
  box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.2);
}

/** Tag component variants end start here **/

/** Tag Component active css start here **/
.tag_component.tag_active {
  background-color: getcolor(theme_color);
  color: getcolor(theme_color);
  border: 1px solid getcolor(theme_color);
  box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.2);
}
.tag_component.tag_active.outline {
  background-color: getcolor(theme_color_opacity);
  border-color: getcolor(theme_color);
  color: getcolor(theme_color);
}
.tag_component.tag_active.outline:hover {
  background-color: getcolor(theme_color_opacity);
}
.tag_component.tag_active.contained {
  background-color: getcolor(theme_color_opacity);
  color: getcolor(theme_color);
  border: 1px solid getcolor(theme_color);
  box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.2);
}
.tag_component.tag_active.contained:hover {
  background-color: getcolor(theme_color_opacity);
  color: getcolor(theme_color);
  border: 1px solid getcolor(theme_color);
  box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.2);
}

/** Tag Component css active end here **/

/** Tag Component disable css start here **/
.tag_component.tag_disabled {
  color: getcolor(tag_disable_text_color);
  cursor: default;
  box-shadow: none;
}
.tag_component.tag_disabled:hover {
  color: getcolor(tag_disable_text_color);
  background-color: getcolor(hoc_border_color);
  box-shadow: none;
}
.tag_component.outline.tag_disabled {
  border-color: getcolor(tag_outline_border_disable_color);
  background-color: getcolor(tag_disable_background_color);
}
.tag_component.outline.tag_disabled:hover{
  color: getcolor(tag_disable_text_color);
}
.tag_component.contained.tag_disabled {
  border-color: getcolor(hoc_border_color);
  color: getcolor(tag_disable_text_color);
  background-color: getcolor(hoc_border_color);
  box-shadow: none;
}
.tag_component.contained.tag_disabled:hover {
  border-color: getcolor(hoc_border_color);
  color: getcolor(tag_disable_text_color);
  background-color: getcolor(hoc_border_color);
  box-shadow: none;
}

/** Tag Component Disable css end here **/

.tag_component .tag_outer{
  transition: width .2s linear;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  display: block;
}
.tag_component .tag_outer.toggle_left{
  margin-left: 20px;
}
.tag_component .tag_outer.toggle_right{
  margin-right: 20px;
}
.tag_component.tag_active .checked_icon {
  width: 10px;
  margin-right: 6px;
  position: absolute;
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
  transition: .2s linear;
}
.tag_component .delete_icon {
  width: 8px;
  margin-left: 8px;
  cursor: pointer;
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}
.tag_component.tag_sm .delete_icon {
  width: 12px;
}

@media only screen and (max-width: 1399px){
  .tag_component {
    font-size: 10px;
    line-height: 10px;
    padding: 5px 10px;
  }
}

/** tag component css end here **/