@import '/app/global.scss';

/** simpleProgressBar component css start here **/
.simple_progress_bar_component{
    display: block;
    width: 100%;
    margin: 0 auto;
}
.simple_progress_bar_component.show_steps{
    padding-top:35px;
}
.simple_progress_bar_component .simple_progress_bar_outer{
    display: block;
}
.simple_progress_bar_component .simple_progress_bar_outer.simple_progress_bar_gradient{
    padding: 27px 20px;
    border-radius: 5px;
    background: linear-gradient(90deg, getcolor(startup_progressbar), transparent);
}
.simple_progress_bar_component .simple_progress_bar_outer .simple_progress_bar_child_content{
    width: 100%;
    display: block;
}
.simple_progress_bar_component .simple_progress_bar_outer .simple_progress_bar_component_container{
    display: grid;
}
.simple_progress_bar_component.show_steps .simple_progress_bar_outer .simple_progress_bar_component_container{
    grid-gap: 6px;
}
.simple_progress_bar_component .simple_progress_bar_outer .simple_progress_bar_component_container .simple_progress_bar_step .simple_progress_bar_content .simple_progress_bar_text{
    color:getcolor(text_color);
    font-family: "Gilroy-Regular";
    font-size: 14px;
    line-height: 14px;
}
.simple_progress_bar_component .simple_progress_bar_outer .simple_progress_bar_component_container .simple_progress_bar_step .simple_progress_bar_content .simple_progress_bar_status{
    color:getcolor(text_color);
    font-size: 10px;
    line-height: 20px;
    width: 20px;
    height: 20px;
    font-family: "Gilroy-Bold";
    margin-right: 10px;
    text-align: center;
    border-radius: 50%;
    background-color: getcolor(simple_progress_circle_bacground_color);
    flex-shrink: 0;
}
.simple_progress_bar_component .simple_progress_bar_outer .simple_progress_bar_component_container .simple_progress_bar_step {
    position: relative;
}
.simple_progress_bar_component .simple_progress_bar_outer .simple_progress_bar_component_container .simple_progress_bar_step .simple_progress_bar_content{
    text-align: left;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 10px;
}
.simple_progress_bar_component .simple_progress_bar_outer.simple_progress_bar_gradient .simple_progress_bar_component_container .simple_progress_bar_step .simple_progress_bar_content{
    position: absolute;
    left: 0;
    right: 0;
    top: -60px;
}
.simple_progress_bar_component .simple_progress_bar_outer .simple_progress_bar_component_container .simple_progress_bar_step .simple_progress_bar_content .simple_progress_bar_status.active_bar_status{
    color: getcolor(theme_color);
}
.simple_progress_bar_component .simple_progress_bar_outer .simple_progress_bar_component_container .simple_progress_bar_step .simple_progress_bar_content .simple_progress_bar_status.bar_completed{
    background-color: getcolor(theme_color);
    line-height: unset;
    background-image: url('../../icons/tick.svg');
    background-size: 8px;
    background-position: center center;
    background-repeat: no-repeat;
}
.simple_progress_bar_component .simple_progress_bar_component_container .simple_progress_bar_step:first-child .simple_progress_bar_primary{
    border-radius: 3px 0 0 3px ;
}
.simple_progress_bar_component .simple_progress_bar_component_container .simple_progress_bar_step:last-child .simple_progress_bar_primary{
    border-radius: 0 3px 3px 0;
}
.simple_progress_bar_component .simple_progress_bar_outer .simple_progress_bar_component_container .simple_progress_bar_step .simple_progress_bar_primary{
    height: 5px;
    width: 100%;
}
.simple_progress_bar_component.show_steps .simple_progress_bar_outer .simple_progress_bar_component_container .simple_progress_bar_step .simple_progress_bar_primary{
    border-radius: 3px;
}


/** simpleProgressBar component media query css start here **/



@media only screen and (max-width: 1600px){
    .simple_progress_bar_component.show_steps{
        padding-top: 28px;
    }
    .simple_progress_bar_component .simple_progress_bar_outer.simple_progress_bar_gradient {
        padding: 23px 20px;
    }
    .simple_progress_bar_component .simple_progress_bar_outer .simple_progress_bar_component_container .simple_progress_bar_step .simple_progress_bar_primary{
        height: 4px;
    }
    .simple_progress_bar_component .simple_progress_bar_outer .simple_progress_bar_component_container .simple_progress_bar_step .simple_progress_bar_content .simple_progress_bar_text{
        font-size: 12px;
        line-height: 12px;
    }
    .simple_progress_bar_component .simple_progress_bar_outer .simple_progress_bar_component_container .simple_progress_bar_step .simple_progress_bar_content{
        margin-bottom: 6px;
    }
    .simple_progress_bar_component .simple_progress_bar_outer.simple_progress_bar_gradient .simple_progress_bar_component_container .simple_progress_bar_step .simple_progress_bar_content{
        top: -50px;
    }
  

}

@media only screen and (max-width: 1399px){
    .simple_progress_bar_component.show_steps{
        padding-top: 25px;
    }
    .simple_progress_bar_component .simple_progress_bar_outer.simple_progress_bar_gradient{
        padding: 18px 15px;
    }
    .simple_progress_bar_component .simple_progress_bar_outer.simple_progress_bar_gradient .simple_progress_bar_component_container .simple_progress_bar_step .simple_progress_bar_content{
        top: -42px;
    }
    .simple_progress_bar_component .simple_progress_bar_outer .simple_progress_bar_component_container .simple_progress_bar_step .simple_progress_bar_content .simple_progress_bar_status{
        line-height: 18px;
        width: 18px;
        height: 18px;
        margin-right: 8px;
    }
}

@media only screen and (max-width: 1199px){
    .simple_progress_bar_component.show_steps{
        padding-top:22px;
    }
    .simple_progress_bar_component .simple_progress_bar_outer.simple_progress_bar_gradient{
        padding: 14px 15px;
    }
    .simple_progress_bar_component .simple_progress_bar_outer .simple_progress_bar_component_container .simple_progress_bar_step .simple_progress_bar_content .simple_progress_bar_text{
     font-size: 10px;
     line-height: 10px;
    }
    .simple_progress_bar_component .simple_progress_bar_outer .simple_progress_bar_component_container .simple_progress_bar_step .simple_progress_bar_content .simple_progress_bar_status{
        line-height: 16px;
        width: 16px;
        height: 16px;
        font-size: 9px;
    }
    .simple_progress_bar_component .simple_progress_bar_outer .simple_progress_bar_component_container .simple_progress_bar_step .simple_progress_bar_content .simple_progress_bar_status.bar_completed{
        background-size: 6px;
    }
    .simple_progress_bar_component .simple_progress_bar_outer.simple_progress_bar_gradient .simple_progress_bar_component_container .simple_progress_bar_step .simple_progress_bar_content{
        top: -36px;
    }
}

