@import '/app/global.scss';

/** grid component css start here **/
.grid_component.flex{
  display: flex;
  flex: 0 1 auto;
  flex-direction: row;
  flex-wrap: wrap;
  margin-right: -10px;
  margin-left: -10px;
}
.grid_component.autofill{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
}

/** grid component spacing css start here **/
.grid_component.around {
	justify-content: space-around;
}
.grid_component.between {
	justify-content: space-between;
}
.grid_component.sp_5{
  grid-gap: 5px;
}
.grid_component.sp_10{
  grid-gap: 10px;
}
.grid_component.sp_15{
  grid-gap: 10px;
}
.grid_component.sp_20{
  grid-gap: 20px;
}

/** grid component spacing css end here **/

/** grid component alignment css start here **/
.grid_component.start {
	justify-content: flex-start;
}
.grid_component.center {
	justify-content: center;
}
.grid_component.end{
	justify-content: flex-end;
}

/** grid component alignment css end here **/

/** grid component placement css start here **/
.grid_component.top {
	align-items: flex-start;
}
.grid_component.middle {
	align-items: center;
}
.grid_component.bottom {
	align-items: flex-end;
}

/** grid component placement css end here **/
@media only screen and (max-width: 1600px){
  .grid_component.autofill{
    grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  }
}
@media only screen and (max-width: 1399px){
  .grid_component.autofill{
    grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
  }
}
@media only screen and (max-width: 1299px){
  .grid_component.autofill{
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  }
}
@media only screen and (max-width: 1199px){
  .grid_component.autofill{
    grid-template-columns: repeat(auto-fill, minmax(190px, 1fr));
  }
}

/** grid component css end here **/