@import '/app/global.scss';

/** iconBox component css start here **/

.disabled {
  cursor: not-allowed;
}
.icon_box_component {
  display: flex;
  align-items: center;
  justify-content: center;
}
.disabled .icon_box_component {
  opacity: 0.6;
}
.icon_box_component_main {
  position: relative;
}
.icon_box_list_dropdown {
  position: absolute;
  left: 0;
  width: 110px;
  box-shadow: 0 2px 13.8px 1.2px getcolor(list_box_shadow_color);
  background-color: getcolor(menu_card_color);
  border-radius: 5px;
  transform: scale(0);
  transform-origin: top left;
  transition: transform 250ms;
  visibility: hidden;
}
.icon_box_component_main .icon_box_component {
  justify-content: flex-start;
  padding: 0 10px;
  cursor: pointer;
  position: relative;
}

.icon_box_component_main .icon_box_component .iconbox_dropdown_icon {
  width: 7px;
  right: 10px;
  position: absolute;
  top:50%;
  transform: translateY(-50%);
}
.icon_box_component_main.show  .icon_box_list_dropdown {
  visibility: visible;
  transform: scale(1);
}
.icon_box_component_main.show .icon_box_component .iconbox_dropdown_icon {
  transform: translateY(-50%) rotate(180deg);
}
/** iconBox component css for space start here **/
.icon_box_component.sm {
  width: 25px;
  height: 25px;
}
.icon_box_component.md {
  width: 40px;
  height: 40px;
}
.icon_box_component.lg {
  width: 45px;
  height: 45px;
}
/** iconBox component css for space end here **/
.icon_box_component .icon {
  width: 14px;
  display: block;
}

/** iconBox component css for color default, active and fill start here **/
.icon_box_component.outlined {
  border: 1px solid getcolor(icon_box_border_color);
  background-color: getcolor(icon_box_outline_background_color);
}
.icon_box_component.filled {
  border: 1px solid getcolor(theme_color);
  background-color: getcolor(theme_color);
}
.icon_box_component.filled:hover {
  background: getcolor(theme_color_onhover);
  border-color: getcolor(theme_color_onhover);
}
.icon_box_component.filled.active {
  border-color: getcolor(theme_color_onhover);
  background-color: getcolor(theme_color_onhover);
}
.icon_box_component.outlined.active {
  border: 1px solid getcolor(theme_color);
  background-color: getcolor(theme_color);
}
.icon_box_component.default.active {
  background-color: getcolor(icon_box_outline_background_color);
}
.icon_box_component.default:hover {
  background-color: getcolor(icon_box_outline_background_color);
}

/** iconBox component css for color default, active and fill end here **/

@media only screen and (max-width: 1399px) {
  .icon_box_component.md {
    width: 30px;
    height: 30px;
  }
  .icon_box_component.lg {
    width: 40px;
    height: 40px;
  }
}

