@import '/app/global.scss';

/** Accordion Component css start here **/

.accordion {
  border-radius: 5px;
  margin-bottom: 10px;
  position: relative;
}

.accordion.filled{
  background-color: getcolor(accordion_background_color);
}

.accordion.outlined{
  border: 1px solid getcolor(accordion_border_color);
}

.accordion .accordion_link_list {
  cursor: pointer;
  display: flex;
  align-items: center;
  flex-direction: row;
  padding: 15px 0;
}

.accordion .accordion_link_list .accordion_list_draggIcon {
  position: absolute;
  left: -13px;
  width: 5px !important;
  display: none;
  top: 50%;
  transform: translateY(-50%);
}

.accordion .accordion_link_list:hover .accordion_list_draggIcon {
  display: block !important;
}

.accordion.outlined .accordion_link_list, .accordion.filled .accordion_link_list, .accordion.hoverable .accordion_link_list  {
  padding: 15px 10px;
}

.accordion.hoverable .accordion_link_list {
  border-radius: 5px;
}

.accordion.hoverable .accordion_link_list:hover {
  background-color: getcolor(accordion_background_color);
}

.accordion.accordion.disable_accordion .accordion_link_list {
  pointer-events: none;
}

.accordion .accordion_list_icon {
  margin-right: 15px;
  width: 18px;
}

.accordion .accordion_content_toggle {
  flex: 1;
  position: relative;
  color: getcolor(heading_color);
  font-family: 'Gilroy-Bold';
  font-size: 16px;
  line-height: 16px;
  min-width: 0;
  word-break: break-word;
}

.accordion.disable_accordion .accordion_content_toggle {
  color: getcolor(disable_text_color) !important;
}

.accordion  .accordion_toggle_icon {
  transform:rotate(0);
}

.accordion .panel{
  max-height: 0;
  overflow: hidden;
  transition:  max-height 200ms ease-in-out ;
  will-change: max-height;
  color: getcolor(text_color);
}

.accordion.active  .accordion_content_toggle {
  color:getcolor(accordion_active_color);
}

.accordion.active  .accordion_toggle_icon {
  transform: rotate(180deg);
}

/** Accordion Component end start here **/

@media only screen and (max-width: 1600px){

  .accordion .accordion_content_toggle {
    font-size: 14px;
   line-height: 14px;
  }

  .accordion .accordion_list_icon {
    margin-right: 13px;
    width: 14px;
  }

}

@media only screen and (max-width: 1399px){

  .accordion .accordion_content_toggle {
    font-size: 12px;
   line-height: 12px;
  }
  .accordion .accordion_link_list .accordion_list_draggIcon {
    left: -10px;
  }
}

/** Accordion Component end start here **/

@media only screen and (max-width: 1399px){

  .accordion .accordion_list_icon {
    margin-right: 13px;
    width: 13px;
  }

}

@media only screen and (max-width: 1199px){

  .accordion .accordion_content_toggle {
    font-size: 12px;
   line-height: 12px;
  }

  .accordion .accordion_list_icon {
    margin-right: 13px;
    width: 13px;
  }

}
