@import '/app/global.scss';

/** select options component css start here **/
.select_options_component .select_label {
  font-size: 14px;
  line-height: 14px;
  font-family: 'Gilroy-Medium';
  color: getcolor(heading_color);
  margin-right: 15px;
  vertical-align: middle;
  display: inline-block
}
.select_options_component .select_container{
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 145px;
}
.select_options_component .select_container .select_dropdown{
  border: 1px solid getcolor(option_border_color);
  padding: 21px 16px;
  border-radius: 5px;
  padding-right: 25px;
  background-size: 10px;
  background-repeat: no-repeat;
  background-position: right 12px center;
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.select_options_component.disable_select_component .select_dropdown{
  cursor: not-allowed;
  pointer-events: none;
}
.select_options_component .select_container .select_dropdown .select_option_box_icon{
  width: 14px;
  margin-right: 10px;
}
.select_options_component .select_container .select_dropdown span{
  font-size: 16px;
  line-height: 16px;
  font-family: 'Gilroy-Regular';
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: list-item;
  color: getcolor(option_title_color);
}
.select_options_component.disable_select_component  .select_container .select_dropdown span{
  color: getcolor(disable_text_color);
}
.select_options_component .select_container .select_animate{
  position: absolute;
  top: 50%;
  left: 0;
  font-size: 16px;
  line-height: 16px;
  font-family: 'Gilroy-Regular';
  color: getcolor(text_color);
  transition: font-size 0.2s ease, transform 0.2s ease, transform-origin 0.2s , top 0.2s;
  transform: translate(0, -50%) scale(1);
  transform-origin: left top;
  background-color: getcolor(dashboard_background);
  margin: 0 8px;
  padding: 0 8px;
}
.select_options_component .select_container.slide_up .select_animate {
  color: getcolor(text_color);
  font-size: 75%;
  line-height: 16px;
  top: 0;
  transform: translate(0, -8px) scale(0.9);
}
.select_options_component .select_container .select_icon{
  position: absolute;
  top:50%;
  transform: translateY(-50%);
  right: 12px;
}
.select_options_component .select_container .select_icon img{
  display: block;
  width: 9px;
}
.select_options_component .select_container.rotate  .select_icon img{
  transform: rotate(180deg);
}
.select_options_component .select_container .select_box{
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 1;
  text-align: left;
  box-shadow: 0 15px 20px 0 getcolor(list_box_shadow_color);
  border: 0;
  padding: 0;
  background: getcolor(select_background);
}

.select_options_component .select_container.rotate .select_box{
  display: block;
}

.select_options_component .select_container .select_list_icon{
  width: 12px;
  display: block;
}
.select_options_component .select_container .select_box ul li{
  padding: 12px 20px;
}
.select_options_component .select_container .select_box ul li.selected{
   background-repeat: no-repeat;
   background-position: center left 6px;
   background-size: 8px;
}


.select_options_component .select_container .select_box ul li > div:first-child{
  font-size: 16px;
  line-height: 16px;
}
@media only screen and (max-width: 1600px){
  .select_options_component .select_container .select_dropdown{
    padding: 16px 16px;
    padding-right: 25px;
  }
  .select_options_component .select_container .select_dropdown span{
    font-size: 14px;
    line-height: 16px;
  }
  .select_options_component .select_container .select_animate{
    font-size: 14px;
    line-height: 14px;
  }
  .select_options_component .select_container.slide_up .select_animate {
    line-height: 14px;
    transform: translate(0, -7px) scale(0.9);
  }
  .select_options_component .select_container .select_box ul li > div:first-child{
    font-size: 14px;
    line-height: 16px;
  }
  .select_options_component .select_container .select_dropdown .select_option_box_icon{
    width: 12px;
  }
}
@media only screen and (max-width: 1399px){
  .select_options_component .select_label {
    font-size: 12px;
    line-height: 12px;
    margin-right: 15px;
  }
  .select_options_component .select_container{
    width: 135px;
  }
  .select_options_component .select_container .select_dropdown{
    padding: 12px 12px;
    padding-right: 20px;
  }

  .select_options_component .select_container .select_dropdown span{
    font-size: 12px;
    line-height: 14px;
  }
  .select_options_component .select_container .select_animate{
    font-size: 12px;
    line-height: 12px;
    margin: 0 6px;
    padding: 0 6px;
  }
  .select_options_component .select_container.slide_up .select_animate {
    line-height: 12px;
    transform: translate(0, -6px) scale(0.9);
  }
  .select_options_component .select_container .select_icon img{
    width: 6px;
  }
  .select_options_component .select_container .select_list_icon{
    width: 10px;
  }
  .select_options_component .select_container .select_box ul li{
    padding: 9px 20px;
  }
  .select_options_component .select_container .select_box ul li > div:first-child{
    font-size: 12px;
    line-height: 12px;
  }
  .select_options_component .select_container .select_dropdown .select_option_box_icon{
    width: 10px;
  }
}
@media only screen and (max-width: 1299px){
  .select_options_component .select_container{
    width: 125px;
  }
}

/** select options component css end here **/