.create_new_project_select_cms_container {
  display: flex;
  flex-direction: row;
}
.visibility_hidden {
  opacity: 0;
  visibility: hidden;
  transform: translateX(60px);
  position: absolute;
  top: 0;
  z-index: -1;
}
.visibility_on {
  transform: translateX(0);
  transition: opacity 0.2s, transform 0.2s ease;
  transition-delay: 0.1s;
  visibility: visible;
  opacity: 1;
}
.slide_prev {
  transform: translateX(-60px);
  opacity: 0;
  visibility: hidden;
  position: absolute;
}

