@import '/app/global.scss';

/** Checkbox Component css start here **/
.checkbox_component.checkbox_block{
    display: block;
}
.checkbox_component.checkbox_inline_block{
    display: inline-block;
    vertical-align: middle;
}

/** Checkbox Component placement css start here **/
.checkbox_component.right{
    position: absolute;
    right: 10px;
}
.checkbox_component.left{
    position: absolute;
    left: 10px;
}

/** Checkbox Component placement css end here **/

.checkbox_component .checkbox_container{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.checkbox_component .checkbox_container .checkbox_label_left{
    font-size: 14px;
    line-height: 14px;
    margin-right: 5px;
    color: getcolor(text_color);
}
.checkbox_component .checkbox_container .checkbox_label_right{
    font-size: 14px;
    line-height: 14px;
    margin-left: 10px;
    color: getcolor(text_color);
}
.checkbox_component .checkbox_container label {
    position: relative;
    cursor: pointer;
    display: block;
    width: 16px;
    height: 16px;
    flex-shrink: 0;
}
.checkbox_component .checkbox_container input {
    display: none;
    cursor: pointer;
}

.checkbox_component.disable .checkbox_container .checkbox_label_left, .checkbox_component.disable .checkbox_container .checkbox_label_right{
    opacity: 0.3;
}
.checkbox_component .checkbox_container .checkmark {
    background-color: transparent;
    border: 1px solid getcolor(checkbox_default_border_color);
    cursor: pointer;
    width: 16px;
    height: 16px;
}
.checkbox_component.outlined .checkbox_container .checkmark{
    border-color: getcolor(checkbox_default_border_color);
}
.checkbox_component.disable .checkbox_container .checkmark {
    cursor: not-allowed;
}

.checkbox_component .checkbox_container input + .checkmark.checked {
    background-repeat: no-repeat;
    background-size: 8px;
    background-position: center center;
    background-color: getcolor(theme_color);
    border-color: getcolor(theme_color);
}
.checkbox_component.outlined .checkbox_container input + .checkmark.checked {
    border-color: getcolor(theme_color);
    background-color: transparent;
}



/** Checkbox Component css end here **/

/** Responsive css start from here  ***/

@media only screen and (max-width: 1399px) {

  .checkbox_component .checkbox_container .checkbox_label_left{
    font-size: 11px;
    margin-right: 5px;
}

.checkbox_component .checkbox_container .checkbox_label_right{
    font-size: 11px;
    line-height: 11px;
    margin-right: 5px;

}

.checkbox_component .checkbox_container label {
  position: relative;
  cursor: pointer;
  display: block;
  width: 14px;
  height: 14px;
  flex-shrink: 0;
}

.checkbox_component .checkbox_container .checkmark {
  cursor: pointer;
  width: 14px;
  height: 14px;
}

.checkbox_component .checkbox_container input + .checkmark.checked {
  background-size: 6px;

}

}

