@import '/app/global.scss';

/**  textHighlighter component css code start here **/
.texthighlighter_component {
  position: relative;
  font-size: 14px;
  line-height: 14px;
  color: getcolor(heading_color);
}
.texthighlighter_component .overflow_content {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: inline-block;
  max-width: 100%;
}
.texthighlighter_component.highlighter .overflow_content{
  cursor: pointer;
}
.texthighlighter_component.highlighter::after {
  visibility: hidden;
  position: absolute;
  left: -10000px;
  content: attr(tool-data);
}
.texthighlighter_component.highlighter:hover::after {
  left: 0;
  padding: 5px 8px;
  background: getcolor(texthighlighter_background_color);
  border: 1px solid getcolor(texthighlighter_border_color);
  border-radius: 5px;
  box-shadow: 0 15px 40px 0 getcolor(texthighlighter_box_shadow_color);
  overflow-wrap: break-word;
  word-break: break-word;
  visibility: visible;
  white-space: normal;
  z-index: 2;
  max-width: 600px;
  font-size: 11px ;
  line-height: 13px;
  font-family: 'Gilroy-Regular';
  color: getcolor(texthighlighter_text_color);
  transition-delay: 0.5s;
}
.texthighlighter_component.top.highlighter::after {
  bottom: 100%;
}
.texthighlighter_component.bottom.highlighter::after {
  top: 100%;
}
.display_none {
  display: none;
}
.texthighlighter_component.disable .overflow_content{
  cursor: not-allowed;
  opacity: 0.9;
}

/**  textHighlighter component css code end here **/