@import '/app/global.scss';

/** Menu Component css start here **/
.menu_component{
    display: block;
}
.menu_component .menu_card{
    cursor: default;
    box-shadow: 0 2px 13.8px 1.2px getcolor(list_box_shadow_color);
    padding: 0;
    text-align: left;
    border: none;
    background-color: getcolor(menu_card_color);
}
.menu_component .menu_card:hover{
    box-shadow: 0 2px 13.8px 1.2px getcolor(list_box_shadow_color);
}

/** Menu Component css end here **/

  