@import '/app/global.scss';

.notification_component {
  width: 100%;
}

/** Notification component block side css start here **/
.notification_component .block_notification_component {
  position: fixed;
  bottom: -100%;
  background-color: getcolor(bottom_notification_background);
  min-width: 350px;
  border-radius: 5px;
  box-shadow: 0 6px 30px 0 rgba(0, 0, 0, 0.1);
}
.notification_component .block_notification_component.slide_up {
  animation-delay: 0.2s;
  animation-duration: 0.6s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-out;
  animation-name: slideup;
}
@keyframes slideup {
  0% {
    bottom: -100%;
    opacity: 0;
    z-index: -1;
    visibility: hidden;
  }
  100% {
    bottom: 15px;
    opacity: 1;
    z-index: 1;
    visibility: visible;
  }
}
.notification_component .block_notification_component.slide_down {
  animation-delay: 0.2s;
  animation-duration: 0.6s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-out;
  animation-name: slidedown;
}
@keyframes slidedown {
  0% {
    bottom: 15px;
    opacity: 1;
    z-index: 1;
    visibility: visible;
  }
  100% {
    bottom: -100%;
    opacity: 0;
    z-index: -1;
    visibility: hidden;
  }
}
.notification_component .block_notification_component::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 3px;
  background-color: getcolor(theme_color);
  border-radius: 5px 0 0 5px;
}
.notification_component .block_notification_component.left {
  left: 15px;
}
.notification_component .block_notification_component.right {
  right: 15px;
}
.notification_component .block_notification_component .notification_close {
  position: absolute;
  right: 8px;
  top: 8px;
  cursor: pointer;
}
.notification_component .block_notification_component .notification_close img {
  display: block;
  width: 8px;
}
.notification_component .block_notification_component .notification_header {
  display: flex;
  align-items: center;
  padding: 14px 16px;
}
.notification_component
  .block_notification_component
  .notification_header
  .notification_icon {
  flex-shrink: 0;
  width: 16px;
  margin-right: 10px;
}
.notification_component
  .block_notification_component
  .notification_header
  .notification_text {
  color: getcolor(text_color);
  font-size: 16px;
  line-height: 16px;
  font-family: 'Gilroy-Bold';
  flex: 1;
}

/** Notification component block side css end here **/

/** Notification component bar css start here **/

.notification_component .notification_component_outer {
  position: fixed;
  left: 0;
  right: 0;
  background-color: getcolor(notification_background_color);
  z-index: 999;
  opacity: 1;
  padding: 0;
 
  
}
.notification_component .notification_component_outer.floating {
  animation-delay: 0.2s;
  animation-duration: 0.3s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-out;
  animation-name: slideInUp;
  bottom: -100%;
}

/*** float bar spacing and animation  start here ***/

.notification_component .notification_component_outer.floating.bottom_space_10 { 
  animation-name: slideInUp_10;
 
}
.notification_component .notification_component_outer.floating.bottom_space_15 {    
  animation-name: slideInUp_15;
 
}
.notification_component .notification_component_outer.floating.bottom_space_20 {  
  animation-name: slideInUp_20;
 
}
.notification_component .notification_component_outer.floating.bottom_space_25{ 
  animation-name: slideInUp_25;
 
}

@keyframes slideInUp {
  0% {
    bottom: -100%;
    opacity: 0;
    z-index: -1;
    visibility: hidden;
  }
  100% {
    bottom: 0px;
    opacity: 1;
    z-index: 999;
    visibility: visible;
  }
}
@keyframes slideInUp_25 {
  0% {
    bottom: -100%;
  
  }
  100% {
    bottom: 25px;  
  }
}
@keyframes slideInUp_20 {
  0% {
    bottom: -100%;
   
  }
  100% {
    bottom: 20px;   
  }
}
@keyframes slideInUp_15 {
  0% {
    bottom: -100%;   
  }
  100% {
    bottom: 15px;   
  }
}
@keyframes slideInUp_10 {
  0% {
    bottom: -100%;   
  }
  100% {
    bottom: 10px; 
  }
}

/*** float bar spacing and animation end here ***/

.notification_component .notification_component_outer.autoremove {
  animation-delay: 0.2s;
  animation-duration: 0.3s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-out;
  animation-name: autoRemove;
  bottom: 15px;
}
@keyframes autoRemove {
  0% {
    bottom: 0px;
    opacity: 1;
    z-index: 999;
    visibility: visible;
  }
  100% {
    bottom: -100%;
    opacity: 0;
    z-index: -1;
    visibility: hidden;
  }
}
.notification_component .notification_component_outer .notification_close {
  position: absolute;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}
.notification_component .notification_component_outer .notification_close > img {
  display: block;
  width: 8px;
}
.notification_component .notification_component_outer::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 1px;
  height: 100%;
  background-color: getcolor(dashboard_background);
}
.notification_component .notification_component_outer.pos_absolute {
  position: absolute;
}
