.notification_component
  .notification_component_outer
  .notification_component_container {
  display: flex;
  align-items: center;
  justify-content: center;
}
.notification_component
  .notification_component_outer
  .notification_component_container.left {
  justify-content: flex-start;
}
.notification_component
  .notification_component_outer
  .notification_component_container.right {
  justify-content: flex-end;
}
.notification_component
  .notification_component_outer
  .notification_component_container
  .notification_icon {
  width: 14px;
  display: block;
  margin-right: 10px;
}
.notification_component
  .notification_component_outer
  .notification_component_container
  .notification_icon.cursor_active {
  cursor: pointer;
}

/** Notification component bar css end here **/
