@import '/app/global.scss';

/**  modal component css start here **/
.modal_component {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1050;
  background-color: getcolor(modal_overlay_background_color);
  animation: fadeInFromNone 200ms ease-in-out;
}

@keyframes fadeInFromNone {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

.modal_component .modal_component_container {
  position: absolute;
  width: 230px;
  margin: 0 auto;
  background-color: getcolor(modal_show_background);
  padding: 20px;
  border-color: getcolor(modal_show_background);
  box-shadow: 0 6px 20px 0 rgba(0, 0, 0, 0.4);
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  cursor: default;
}

/**  modal component placement css start here **/
.modal_component .modal_component_container.top_right {
  top: 20px;
  right: 20px;
  left: unset;
  margin:0;
  transform: none;
}
.modal_component .modal_component_container.top_left {
  top: 20px;
  left: 20px;
  margin:0;
  transform: none;
}
.modal_component .modal_component_container.bottom_left {
  bottom: 20px;
  left: 20px;
  margin:0;
  transform: none;
}
.modal_component .modal_component_container.bottom_right {
  bottom: 20px;
  right: 20px;
  margin:0;
  left: unset;
  transform: none;
}

/**  modal component placement css end here **/

/**  modal component sizes css start here **/

.modal_component .modal_component_container.md {
  width: 450px;
}
.modal_component .modal_component_container.sm {
  width: 300px;
}
.modal_component .modal_component_container.lg {
  width: 600px;
}

.modal_component .modal_component_container.xl{
  width: 700px;
}

/**  modal component sizes css end here **/
.modal_component .modal_component_container .modal_component_loader {
  position: absolute;
  left: 0;
  right: 0;
  top:0;
  bottom:0;
  background-color: getcolor(modal_show_background);
  z-index: 1;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.modal_component .modal_component_container .modal_component_loader_title {
  font-size: 18px;
  line-height: 18px;
  font-family: 'Gilroy-Medium';
  color: getcolor(heading_color);
  margin: 0;
  margin-top: 40px;
}
.modal_component .modal_component_container  .modal_loader_icon img {
  width: 100px;
}
.modal_component .modal_footer .modal_footer_button_group{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 1800px){

  .modal_component .modal_component_container.xl {
    width: 680px;
  }
  .modal_component .modal_component_container.sm {
    width: 280px;
  }
  .modal_component .modal_component_container.lg {
    max-width: 550px;
    width: 100%;

  }
  .modal_component .modal_component_container  .modal_loader_icon img {
    width: 90px;
  }
}





