@import '/app/global.scss';

/** Button component css start here **/
.button_component{
  position: relative;
  text-decoration: none;
  outline: none;
  cursor: pointer;
  font-size: 16px;
  line-height: 16px;
  border-radius: 4px;
  padding: 15px 15px;
  font-family: 'Gilroy-Medium';
  color: getcolor(button_text_color);
  background-color: getcolor(button_background_color);
  border-color: getcolor(button_border_color);
  border-width: 1px;
  border-style: solid;
  display: inline-flex;
  align-items: center;
  justify-content: center;

}
.button_component:hover {
  color: getcolor(button_text_color);
  background-color: getcolor(button_background_hover_color);
  border-color: getcolor(button_border_hover_color);


}

/** button size css start here **/
.button_component.sm{
  padding: 5px 8px;
  min-width: 85px;
  font-size: 12px;
  line-height: 12px;
}
.button_component.md{
  padding: 8px 10px;
  min-width: 95px;
  font-size: 14px;
  line-height: 15px;
}
.button_component.lg{
  padding: 13px 10px;
  min-width: 115px;
  font-size: 16px;
  line-height: 16px;
}

/** button size css end here **/

/** contained button css start here **/
.button_component.contained {
  background-color: getcolor(theme_color);
  border-color: getcolor(theme_color);
  color: #fff;
}

.button_component.contained:hover {
  color: #fff;
  background-color: getcolor(theme_color_onhover);
  border-color: getcolor(theme_color_onhover);
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.2);
}
.button_component.contained.btn_disabled{
  color: #fff;
  background-color: getcolor(theme_color_disable);
  border-color: getcolor(theme_color_disable);
}
.button_component.contained.loading{
  color: #fff;
  background-color: getcolor(theme_color_disable);
  border-color: getcolor(theme_color_disable);
}

/** contained button css end here **/

/** text button css start here **/
.button_component.text{
  background-color: transparent;
  border: 0;
}
.button_component.text span{
  color: getcolor(theme_color);
}
.button_component.text.btn_disabled{
  background-color: transparent;
  opacity: 0.5;
}
.button_component.text.loading{
  background-color: transparent;
  opacity: 0.5;
}
.button_component.text:hover {
  background-color: getcolor(component_tag_text_background_color);
  border-color: getcolor(component_tag_text_background_color);
  border:0;
}
.button_component.btn_disabled span{
  cursor: default;
}
.button_component.loading span{
  cursor: default;
}
.button_component.text:hover span {
  text-decoration: underline;
}



/** text button css end here **/



/** outline button css start here **/
.button_component.outline {
  border: 1px solid getcolor(theme_color);
  color: getcolor(theme_color);
  background-color: transparent;
}
.button_component.outline:focus,
.button_component.outline:active,
.button_component.outline:hover {
  color: #fff;
  background-color: getcolor(theme_color);
  border-color: getcolor(theme_color);
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.2);
}

/** outline button css end here **/

.button_component.btn_block {
  width: 100%;
  display: block;
}
.button_component.btn_rounded {
  border-radius: 10em;
}

/** disabled button css start here **/
.button_component.btn_disabled {
  pointer-events: none !important;
  cursor: not-allowed;
  opacity: 0.9;
  background-color: getcolor(disable_button_background);
  color: getcolor(disable_button_text);
  border: 1px solid getcolor(disable_button_border_color);
}
.button_component.loading {
  pointer-events: none !important;
  cursor: not-allowed;
  opacity: 0.9;
  background-color: getcolor(disable_button_background);
  color: getcolor(disable_button_text);
  border: 1px solid getcolor(disable_button_border_color);
}

/** disabled button css end here **/

/** component positions css start here **/
.button_component.float_left{
  float: left;
}
.button_component.float_right{
  float: right;
}

/** component positions css end here **/

/** deafult outline button css start here **/
.button_component.defaultoutline {
  border: 1px solid getcolor(custom_border_start_site_color);
  color: getcolor(theme_color);
  background-color: transparent;
}
.button_component.defaultoutline:hover,
.button_component.defaultoutline:active {
  color: getcolor(theme_color);
  border: 1px solid getcolor(custom_border_start_site_color);
  background-color: getcolor(custom_background_start_site_color);
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.2);
}
.button_component.defaultoutline.btn_disabled{
  pointer-events: none;
  cursor: not-allowed;
  background-color: getcolor(disable_button_background);
  color: getcolor(disable_button_text);
  border: 1px solid getcolor(disable_button_background);
}
.button_component.defaultoutline.loading{
  pointer-events: none;
  cursor: not-allowed;
  background-color: getcolor(disable_button_background);
  color: getcolor(disable_button_text);
  border: 1px solid getcolor(disable_button_background);
}

/** default outline button css end here **/
.button_component.btn_icon_left img {
  width: 16px;
  margin-right: 14px;
}
.button_component.btn_icon_right img {
  width: 16px;
  margin-left: 14px;
}
.button_component.sm.btn_icon_left img{
  width: 12px;
  margin-right: 10px;
}
.button_component.md.btn_icon_left img{
  width: 14px;
  margin-right: 10px;
}
.button_component.sm.btn_icon_right img{
  width: 12px;
  margin-left: 10px;
}


.button_component.md.btn_icon_right img{
  width: 14px;
  margin-left: 10px;
}



/** Button component css media query for 1299 screen start here **/

@media (max-width: 1299px) {
  .button_component {
    padding: 8px 12px;
  }



}

/** Button component css media query for 1299 screen end here **/

/** Button component css end here **/
