@import 'app/global.scss';

/* Switch component css start here */
.switch_component{
  display: flex;
  flex-direction: row;
  align-items: center;
}
.switch_component .switch_title_left{
  font-size: 16px;
  line-height: 16px;
  font-family: 'Gilroy-Regular';
  color: getcolor(text_color);
  margin-right:10px;
}
.switch_component.sm .switch_title_left{
  font-size: 14px;
  line-height: 14px;
}
.switch_component .switch_title_right{
  font-size: 16px;
  line-height: 16px;
  font-family: 'Gilroy-Regular';
  color: getcolor(text_color);
  margin-left:10px;
}
.switch_component.disabled .switch_title_right, .switch_component.disabled .switch_title_left{
  opacity: 0.3;
}
.switch_component .switch_content{
  flex-shrink: 0;
  display: inline-flex;
}

/* Switch component placement css end here */

.switch_component .switch_content .toggle {
  display: inline-block;
  vertical-align: middle;
  width: 52px;
  height: 30px;
  background: getcolor(switch_inactive_background_color);
  position: relative;
  border-radius: 50px;
  cursor: pointer;
}
.switch_component .switch_content .toggle .toggle_ball {
  width: 20px;
  height: 20px;
  background: #fff;
  position: absolute;
  left: 3px;
  border-radius: 50%;
  top: 50%;
  transform: translateY(-50%);
  transition: left .25s ease;
}
.switch_component .switch_content .toggle.active {
  background: getcolor(theme_color);
}
.switch_component .switch_content .toggle.active .toggle_ball {
  left: calc(100% - 24px);
}
.switch_component.disabled .switch_content .toggle{
  cursor: default;
  background-color: getcolor(text_disable_card);
  opacity: 0.7;
}
.switch_component.disabled .switch_content .toggle .toggle_ball {
  background: getcolor(switch_disable_ball_color);
}

.switch_component.sm .switch_content .toggle {
  width: 26px;
  height: 16px;
}
.switch_component.sm .switch_content .toggle  .toggle_ball{
  width: 10px;
  height: 10px;
}
.switch_component.md .switch_content .toggle  .toggle_ball{
  width: 15px;
  height: 15px;
}
.switch_component.sm .switch_content .toggle.active .toggle_ball {
  left: calc(100% - 13px);
}
.switch_component.md .switch_content .toggle {
  width: 34px;
  height: 20px;
}
.switch_component.md .switch_content .toggle.active .toggle_ball {
  left: calc(100% - 18px);
}

/* Switch component media query for max-width:1600 css start here */

@media only screen and (max-width: 1399px) {
  .switch_component .switch_content .toggle {
    width: 34px;
    height: 20px;
  }

  .switch_component .switch_content .toggle .toggle_ball {
    width: 14px;
    height: 14px;
  }

  .switch_component .switch_content .toggle.active .toggle_ball {
    left: calc(100% - 18px);
  }
}

@media only screen and (max-width: 1024px) {

  .switch_component .switch_content .toggle {
    width: 34px;
    height: 20px;
  }

  .switch_component .switch_content .toggle .toggle_ball {
    width: 14px;
    height: 14px;
  }

  .switch_component .switch_content .toggle.active .toggle_ball {
    left: calc(100% - 18px);
  }

}

/* Switch component media query for max-width:1600 css end here */

/* Switch component css end here */
