@import '/app/global.scss';

/** ListItem css start here **/
.list_item_component {
  width: 100%;
  padding: 8px 15px;
  cursor: pointer;
  display: flex;
  align-items: center;
  position: relative;
}
.list_item_component ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.list_item_component:hover {
  background-color: getcolor(select_hover);
}

.list_item_component.activeOnline {
  background-color: getcolor(theme_color_opacity);
}

.list_item_component .list_item_text {
  flex: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 14px;
  line-height: 15px;
  color: getcolor(sub_heading_color);
  font-family: 'Gilroy-Regular';
}
.list_item_component:hover .list_item_text,
.list_item_component.active .list_item_text {
  color: getcolor(heading_color);
}
.list_item_component.activeOnline .list_item_text {
  color: getcolor(theme_color) !important;
}
.list_item_component.activeOnline:hover .list_item_text {
  color: getcolor(theme_color) !important;
}

.list_item_component.divider {
  border-top: 1px solid getcolor(select_hover);
}
.list_item_component .list_item_header {
  flex-shrink: 0;
  margin-right: 10px;
}
.list_item_component .list_item_header_part {
  line-height: inherit !important;
}

.list_item_outer.active:hover .list_item_text span {
  color: getcolor(theme_color) !important;
}

.list_item_outer.active:hover {
  background-color: transparent !important;
}

.list_item_outer:first-child .list_item_component {
  border-radius: 5px 5px 0 0;
}
.list_item_outer:last-child .list_item_component {
  border-radius: 0 0 5px 5px;
}
.list_item_outer:first-child:last-child .list_item_component {
  border-radius: 5px;
}

.list_item_component .list_item_secondary {
  white-space: normal;
  font-size: 12px;
  line-height: 20px;
  font-family: 'Gilroy-Regular';
  color: getcolor(subtitle_text_color);
}
.list_item_component:hover .list_item_secondary,
.list_item_component.active .list_item_secondary {
  color: getcolor(heading_color);
}
.list_item_component .list_item_footer {
  flex-shrink: 0;
  align-items: center;
  display: flex;
  margin-left: 10px;
}
.list_item_component .list_tooltip {
  visibility: hidden;
  position: absolute;
  left: -1000000%;
}
.list_item_component.tooltip_show .list_tooltip {
  z-index: 1;
  width: 75%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 10px;
  line-height: 12px;
  font-family: 'Gilroy-Medium';
  color: getcolor(tooltip_title_color);
  padding: 5px 6px;
  border-radius: 4px;
  background: getcolor(tooltip_default_background);
  box-shadow: 0 0 7px 0 rgba(0, 0, 0, 0.05);
  overflow-wrap: break-word;
  text-align: center;
  bottom: calc(100% + 4px);
  animation: tooltips-hori 0.3s ease forwards;
  animation-delay: 0.4s;
}
.list_item_component.tooltip_show .list_tooltip::after {
  content: '';
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-top: 4px solid getcolor(tooltip_default_background);
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
}

.list_item_component.tooltip_show:first-child .list_tooltip {
  left: 50%;
  top: calc(100% + 4px);
  bottom: auto;
  animation: tooltips-hori 0.3s ease forwards;
  animation-delay: 0.4s;
}
.list_item_component.tooltip_show:first-child .list_tooltip::after {
  content: '';
  position: absolute;
  bottom: 100%;
  left: 50%;
  top: auto;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-bottom: 4px solid getcolor(tooltip_default_background);
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
  border-top: 0;
}

@keyframes tooltips-hori {
  from {
    visibility: hidden;
    opacity: 0;
    transform: translate(-50%, -10%);
  }
  to {
    visibility: visible;
    opacity: 1;
    transform: translate(-50%, 0);
  }
}

@media only screen and (max-width: 1399px) {
  .list_item_component {
    padding: 6px 10px;
  }
  .list_item_component .list_item_text {
    font-size: 12px;
    line-height: 13px;
  }
  .list_item_component .list_item_secondary {
    font-size: 10px;
    line-height: 16px;
  }
}

/** ListItem css end here **/
