@import '/app/global.scss';

/** Loader component css start here **/
.loader_component {
  display: block;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 9999999;
  background: getcolor(dashboard_background);
}

.loader_component .loader_container {
  display: table;
  width: 100%;
  height: 100%;
}

.loader_component .loader_content {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}

/** Loader component css end here **/