@import '/app/global.scss';

/** footer css start here **/

.footer_component {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  font-size: 14px;
  line-height: 18px;
  color: getcolor(text_color);
  font-family: 'Gilroy-Regular';
  padding: 0;
  font-weight: 100;
  margin-bottom: 40px;
}
.footer_component strong {
  font-family: 'Gilroy-Bold';
}

/** footer css end here **/

/** footer css media query for 1024 screen start here **/

@media only screen and (max-width: 1600px) {

  .footer_component {
    font-size: 14px;
    line-height: 14px;
  }
}


@media only screen and (max-width: 1399px) {

  .footer_component {
    font-size: 12px;
    line-height: 12px;
    margin-bottom: 30px;
  }

}

@media only screen  and (max-width: 1199px) {


  .footer_component {
    font-size: 11px;
    line-height: 11px;
  }

}

/** footer css media query for 1024 screen end here **/
