@import '/app/global.scss';

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 9999;
  height: 100%;
}
.notification_component {
  width: 100%;
}

/** Notification component block side css start here **/
.notification_component .block_notification_component {
  position: fixed;
  right: -100%;
  background-color: getcolor(bottom_notification_background);
  min-width: 350px;
  border-radius: 5px;
  box-shadow: 0 6px 30px 0 rgba(0, 0, 0, 0.1);
}

.notification_component .block_notification_component::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  width: 3px;
  background-color: getcolor(theme_color);
  border-radius: 5px 0 0 5px;
}
.notification_component .block_notification_component.left {
  left: 15px;
}
.notification_component .block_notification_component.right {
  right: 15px;
}

.notification_component .block_notification_component .notification_header {
  display: flex;
  align-items: center;
  padding: 14px 16px;
}
.notification_component
  .block_notification_component
  .notification_header
  .notification_icon {
  flex-shrink: 0;
  width: 16px;
  margin-right: 10px;
}
.notification_component
  .block_notification_component
  .notification_header
  .notification_text {
  color: getcolor(text_color);
  font-size: 16px;
  line-height: 16px;
  font-family: 'Gilroy-Bold';
  flex: 1;
}

/** Notification component block side css end here **/

/** Notification component bar css start here **/

.notification_component .notification_component_outer.error {
  border-left: 4px solid #d93025;
}
.notification_component .notification_component_outer.warning {
  border-left: 4px solid #2477f3;
}
.notification_component .notification_component_outer.success {
  border-left: 4px solid #009e62;
}
.notification_component .notification_component_outer {
  position: fixed;
  right: 30px;
  background-color: getcolor(notification_side_background_color);
  z-index: 999;
  opacity: 1;
  top: 40px;
  box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.2);
  padding: 15px 30px 15px 10px;
  max-width: 500px;
  line-height: 16px;
  border-radius: 5px;
}
.notification_component .notification_component_outer.floating {
  animation-delay: 0.2s;
  animation-duration: 0.3s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-out;
  animation-name: slideIn;
  right: -100%;
}

/*** float bar spacing and animation  start here ***/

@keyframes slideIn {
  0% {
    right: -100%;
    opacity: 0;
    z-index: -1;
    visibility: hidden;
  }
  100% {
    right: 30px;
    opacity: 1;
    z-index: 999;
    visibility: visible;
  }
}

/*** float bar spacing and animation end here ***/

.notification_component .notification_component_outer.autoremove {
  animation-delay: 0.3s;
  animation-duration: 0.3s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-out;
  animation-name: autoRemove;
  right: 30px;
}
@keyframes autoRemove {
  0% {
    right: 30px;
    opacity: 1;
    z-index: 999;
    visibility: visible;
  }
  100% {
    right: -100%;
    opacity: 0;
    z-index: -1;
    visibility: hidden;
  }
}

.notification_component .notification_component_outer::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 1px;
  height: 100%;
  background-color: getcolor(dashboard_background);
}
.notification_component .notification_component_outer.pos_absolute {
  position: absolute;
}

.notification_component
  .notification_component_outer
  .notification_component_container {
  display: flex;
  align-items: center;
  justify-content: center;
  color: getcolor(heading_color);
}
.notification_component
  .notification_component_outer
  .notification_component_container.left {
  justify-content: flex-start;
}
.notification_component
  .notification_component_outer
  .notification_component_container.right {
  justify-content: flex-end;
}
.notification_component
  .notification_component_outer
  .notification_component_container
  .notification_icon {
  margin-right: 10px;
}
/*** Message block start here ***/
.notification_component
  .notification_component_outer
  .notification_component_container
  .notification_close_icon {
  width: 5px;
  display: block;
  position: absolute;
  right: 10px;
  align-self: flex-start;
  cursor: pointer;
  top: 8px;
}
.notification_component
  .notification_component_outer
  .notification_component_container
  .notification_message_block {
  display: flex;
  align-items: center;
  padding: 0px;
  height: 100%;
}
.notification_component
  .notification_component_outer
  .notification_component_container
  .notification_message_block
  .notification_message_block_title {
  width: 100%;
  margin-bottom: 0;
  font-size: 14px;
  line-height: 14px;
}
.notification_component
  .notification_component_outer
  .notification_component_container
  .notification_message_block
  .notification_icon {
  display: flex;
}
.notification_component
  .notification_component_outer
  .notification_component_container
  .notification_message_block
  .notification_icon
  img {
  width: 12px;
}

.notification_component
  .notification_component_outer
  .notification_component_container
  .notification_message_block
  .notification_message_block_button {
  min-width: auto;
}
.notification_component
  .notification_component_outer
  .notification_component_container
  .notification_message_block
  .notification_message_block_message {
  color: getcolor(heading_color);
  font-size: 12px;
  line-height: 12px;
  margin-bottom: 0;
  margin-top: 0;
}
.notification_component
  .notification_component_outer
  .notification_component_container
  .notification_message_block
  .notification_message_block_message_margin_top {
  margin-top: 5px;
}

/*** Message block end  here ***/
/** Notification component bar css end here **/
