@import '/app/global.scss';

/*** Card Component css start here **/
.card_component {
  width: 100%;
  border-radius: 5px;
  background-color: getcolor(radio_card_background_color);
  text-align: center;
  cursor: pointer;
  border: 1px solid getcolor(radio_card_border_color);
  padding: 10px 0;
}
.card_component:hover{
  box-shadow: 0 5px 13.8px 1.2px getcolor(card_box_shadow_color);
  background-color: getcolor(card_background_color_hover);
}
.card_component.selected {
  border-color: getcolor(theme_color);
  box-shadow: none;
  background-color: getcolor(radio_card_background_color);
}
.card_component.disabled{
  cursor: not-allowed;
}
.card_component:hover.disabled{
  box-shadow: none;
  background-color: getcolor(radio_card_background_color);
}


/*** Card Component css end here **/