@import '/app/global.scss';

.search_bar_component {
  position: relative;
  outline: none;
  box-shadow: none;
}
.search_bar_component .search_block {
  position: relative;
}

.search_bar_component .search_block .search_bar_icon {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}
.search_bar_component .search_block .search_bar_icon img {
  width: 15px;
  cursor: pointer;
  display: block;
}
.search_bar_component .search_block .search_bar_icon.clear_icon img{
  width: 8px;
}
.search_bar_component .search_block input {
  width: 100%;
  padding: 11px 12px;
  border: 1px solid getcolor(input_border_color);
  font-family: 'Gilroy-Regular';
  font-size: 14px;
  color: getcolor(search_text_color);
  background-color: getcolor(serachbar_input_background);
  padding-right: 30px;
  outline: none;
}
.search_bar_component .search_block input::placeholder {
  color: getcolor(search_text_color);
}
.search_bar_component .search_bar_result_box {
  position: absolute;
  left: 0;
  right: 0;
  display: none;
  z-index: 2;
}
.search_bar_component .search_bar_result_box.show {
  display: block;
}
.search_bar_component .search_bar_result_box .search_bar_card {
  border: none;
  text-align: left;
  padding: 0;
}
.search_bar_component .search_bar_result_box .search_bar_card > div {
  padding: 10px 0;
}

@media screen and (max-width: 1399px) {
  .search_bar_component .search_block input {
    padding: 7px 10px;
    font-size: 12px;
    line-height: 12px;
    padding-right: 25px;
  }
  .search_bar_component .search_block .search_bar_icon img {
    width: 12px;
  }
}

/** Search Bar Component css end here **/
