@import '/app/global.scss';

/** select options component css start here **/
.button_dropdown_component {
  position: relative;
  display: flex;
}
.button_dropdown_component .button_dropdown_container {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: row;
}
.button_dropdown_component .button_dropdown {
  text-decoration: none;
  outline: none;
  cursor: pointer;
  font-size: 14px;
  line-height: 14px;
  border-radius: 4px;
  padding: 12px 15px;
  font-family: 'Gilroy-Medium';
  color: getcolor(button_text_color);
  background-color: getcolor(button_background_color);
  border-color: getcolor(button_border_color);
  border-width: 1px;
  border-style: solid;
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  min-width: 95px;
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
  border-right: 0;
}
.button_dropdown_component .button_dropdown:hover {
  color: getcolor(button_text_color);
  border-color: getcolor(button_border_hover_color);
  background-color: getcolor(button_background_hover_color);
}

.button_dropdown_component .button_dropdown .button_dropdown_icon {
  width: 14px;
  display: block;
  margin-right: 10px;
}
.button_dropdown_component .button_dropdown_container .button_dropdown_box {
  width: 22px;
  align-items: center;
  display: inline-flex;
  justify-content: center;
  border: 1px solid getcolor(button_border_color);
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
  height: 100%;
  cursor: pointer;
  background-color: getcolor(button_background_color);
}
.button_dropdown_component .button_dropdown:hover + .button_dropdown_box {
  border-color: getcolor(button_border_hover_color);
  background-color: getcolor(button_background_hover_color);
}
.button_dropdown_component
  .button_dropdown_container
  .button_dropdown_box
  .button_dropdown_box_icon {
  width: 7px;
  display: block;
}
.button_dropdown_component
  .button_dropdown_container.rotate
  .button_dropdown_box
  .button_dropdown_box_icon {
  transform: rotate(180deg);
}
.button_dropdown_component .button_dropdown_card {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 1;
  padding: 0;
  border: 0;
  text-align: left;
}
.button_dropdown_component .button_dropdown_card ul li {
  padding: 12px 25px;
}
.button_dropdown_component .button_dropdown_card ul li.selected {
  background-repeat: no-repeat;
  background-position: center left 12px;
  background-size: 8px;
}
.button_dropdown_component .button_dropdown_card ul li > div:first-child {
  font-size: 14px;
  line-height: 14px;
}
@media only screen and (max-width: 1600px) {
  .button_dropdown_component .button_dropdown_card ul li {
    padding: 8px 25px;
  }
  .button_dropdown_component .button_dropdown_card ul li > div:first-child {
    font-size: 12px;
    line-height: 12px;
  }
  .button_dropdown_component .button_dropdown {
    font-size: 12px;
    line-height: 12px;
    padding: 10px 10px;
    min-width: 85px;
  }
  .button_dropdown_component .button_dropdown .button_dropdown_icon {
    width: 12px;
  }
  .button_dropdown_component .button_dropdown_card ul li.selected {
    background-size: 7px;
  }
}
@media only screen and (max-width: 1399px) {
  .button_dropdown_component .button_dropdown {
    font-size: 10px;
    line-height: 10px;
    min-width: 75px;
    padding: 9px 10px;
  }
  .button_dropdown_component .button_dropdown_card ul li > div:first-child {
    font-size: 10px;
    line-height: 10px;
  }
  .button_dropdown_component .button_dropdown .button_dropdown_icon {
    width: 10px;
  }
}

/** select options component css end here **/
